import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import {AuthService} from "../services/auth.service";
import {SpellsService} from "../services/spells.service";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/auth/sign-in/sign-in.module#SignInPageModule', name: 'SignInPage', segment: 'sign-in', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/auth/sign-up/sign-up.module#SignUpPageModule', name: 'SignUpPage', segment: 'sign-up', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/spells/create-spell/create-spell.module#CreateSpellPageModule', name: 'CreateSpellPage', segment: 'create-spell', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/spells/spell-list/spell-list.module#SpellListPageModule', name: 'SpellListPage', segment: 'spell-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/spells/spell-list/single-spell/single-spell.module#SingleSpellPageModule', name: 'SingleSpellPage', segment: 'single-spell', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthService,
    SpellsService
  ]
})
export class AppModule {}



